*{
   margin: 0;
   padding: 0;
   box-sizing: border-box;
}

body{
   background-color: black;
}

.container{
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.calc{
  padding: 10px;
  border-radius: 20px;
  background: transparent;
  border: none;
  box-shadow: -1px -1px 10px 10px rgba(189,97,97,0.5);
}

#input{
  width: 100%;
  max-width: 400px;
  border-radius: 20px;
  height: 100px;
  border: none;
  background: transparent;
  text-align: center;
  color: #fff;
  cursor: pointer;
}

button{
  border: none;
  color: #fff;
  margin: 10px;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  /* background: transparent; */
  background-color: blue;
  cursor: pointer;
  box-shadow: 6px 6px 10px rgba(190,97,97,1), -6px -6px 10px rgba(190,97,97,1);
}